import React, { useState } from 'react';
import DatePicker from "react-datepicker";
import { Link } from 'react-router-dom';
import './Write.css';
import profile from './profile_image.jpg';
import "react-datepicker/dist/react-datepicker.css";

function Write() {

    const [meetDate, setmeetDate] = useState(new Date());
    return(
        <div className="makeBlock">
            <div className="Project">
                <Link to="/main">
                    <div className="project_title"><strong>DASOM</strong></div>
                </Link>
                <div className="profile">
                    <Link to="/mypage">
                        <img src={profile} width="40" height="40"/>
                    </Link>
                </div>
            </div>
            <h1 className="header">  or   .</h1>
            <hr />
            <p></p>
            <select className="sizeSet">
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
            </select>
            <p></p>
            <select className="sizeSet">
                <option value="1:1">1:1</option>
                <option value="2:2">2:2</option>
                <option value="3:3">3:3</option>
                <option value="4:4">4:4</option>
                <option value="5:5">5:5</option>
            </select>
            <p></p>
            <DatePicker
                className="sizeSet"
                dateFormat="yyyy/MM/dd"
                selected={meetDate}
                onChange={date => setmeetDate(date)}
                minDate={new Date()}
            />
            <p></p>
            <select className="sizeSet">
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
            </select>
            <h1 className="header"> or   .</h1>
            <hr />
            <p></p>
            <input type="text" id="title" placeholder="  ."/>
            <textarea id="content" placeholder="  ."/>
            <Link to="/main">
                <button className="cancel_button"></button>
            </Link>
            <Link to="/read">
                <button className="upload_button"> </button>
            </Link>
        </div>
    );
}

export default Write;