import React from 'react';
import { Link } from 'react-router-dom';
import './Read.css';
import profile from './profile_image.jpg';

function Read() {

    return(
        <div className="makeBlock">
            <div className="Project">
                <Link to="/main">
                    <div className="project_title"><strong>DASOM</strong></div>
                </Link>
                <div className="profile">
                    <Link to="/mypage">
                        <img src={profile} width="40" height="40"/>
                    </Link>
                </div>
            </div>
            <h1 className="header">  F3   ~~</h1>
            <div className="post_info">
                <div className="writer_profile">
                    <img src={profile} width="45" height="45"/>
                </div>
                <div className="writer_id">F3</div>
                <div className="write_date">2023.09.18</div>
            </div>
            <hr />
            <div className="recruit_info">
                <strong></strong> 
            </div>
            <div className="recruit_info">
                <strong></strong> 3:3
            </div>
            <div className="recruit_info">
                <strong></strong> 2023/10/20
            </div>
            <div className="recruit_info">
                <strong></strong> 
            </div>
            <hr />
            <h3 className="post_content">
                    F3  20,      .
            </h3>
            <Link to="/meetingreq">
                <button className="apply_button"></button>
            </Link>
        </div>
    );
}

export default Read;