import React ,{useState} from 'react';
import { Link } from 'react-router-dom';
import profile from './profile_image.jpg'
import './MyPage.css'
import { getByDisplayValue } from '@testing-library/react';

function MyPage() {
  let RequestedInfo = [];
  const [currentPage, pageChange] = React.useState('1');
  const [reqInfo, reqInfoSet] = React.useState([]);
  const [partInfo, partInfoSet] = React.useState([]);

  let reqInfoAdd = [] //     
  let partInfoAdd = [] //     

  const addReqInfo = (reqInfoAdd) => {
    reqInfoSet(prevState => [...prevState, reqInfoAdd]);
  }
  const addPartInfo = (partInfoAdd) => {
    partInfoSet(prevState => [...prevState, partInfoAdd]);
  }

  function RequestAndParticipate(Univ, Header, HeadCount, UserName) {
    const univ = ''; // Univ    
    const header = ' '; // Header    
    const headCount = '3'; // HeadCount    
    const userName = '' // UserName    

    const boxStyle = {
      border: '2px solid',
      borderColor: 'gray',
      borderRadius: '40px',
      height: '200px',
      margin: '30px',
      marginRight: '70px',
    }

    const contentStyle = {
      marginLeft: '30px',
    }

    const univStyle = {
      fontSize: '12px',
      padding: '7px',
      backgroundColor: 'gray',
      color: 'white',
      borderRadius: '14px',
      textAlign: 'center',
    }

    const countStyle = {
      fontSize: '12px',
      padding: '7px',
      paddingLeft: '30px',
      paddingRight: '30px',
      backgroundColor: 'rgb(26, 188, 156)',
      color: 'white',
      borderRadius: '14px',
      textAlign: 'center',
    }

    const headerStyle = {
      marginBottom: '40px',
    }

    const userStyle = {
      fontSize: '12px',
    }

    return (
      <div style={boxStyle}>
        <p style={contentStyle}>
          <span style={univStyle}>
            {univ}
          </span>
          <h3 style={headerStyle}>
            {header}
          </h3>
          <span style={countStyle}>
            {headCount}
          </span>
          <hr />
          <span style={userStyle}>
            <img />
            {userName}
          </span>
        </p>
      </div>
    );
  }
  //request participate         output  request      participate  

  function Info() {
    const ages = Array.from({length: 10}, (_, i) => i + 20);
    const imgContainerStyle = {
      position: 'relative',
      display: 'inline-block',
      width: '200px',
      height: '230px', // Change this
      marginTop: '30px'
    };
  
    const imgStyle = {
      display: 'block',
      width: '100%',
      height: '87%', // Change this
    };
  
    const buttonStyle = {
     position:'absolute', 
     bottom:'0', 
     right:'0'
  };
    function UnivInfo() {
      //   api
    }

    function currentPenalty() {
      //    api
    }
    
    

    return (
      <div>
        <div style={imgContainerStyle}>
          <img src={profile} style={imgStyle}/>
          <button style={buttonStyle}>
             
          </button>
      </div>
        <form action="" method="POST">
          <label>
            <h3></h3>
            <input type="text" value=" " className="nickNameInput"/>
          </label>
          <h3></h3>
          <UnivInfo />
          <label>
            <h3></h3>
            <select className="ageSelect">
              {ages.map((number) => 
                <option key={number} value={number}>
                  {number}
                </option>)}
            </select>
          </label><br />
          <label>
            <button> </button>
          </label>
        </form>
        //    
      </div>
    );
  }

  function output() {
    if(currentPage === '1'){
      return <RequestAndParticipate />;
    }
    if(currentPage === '2'){
      return <RequestAndParticipate />;
    }
    if(currentPage === '3'){
      return <Info />;
    }
  }

  function Colorchange(Value) {
    const disabledStyle = {
      color: 'gray',
      fontSize: '20px',
      border: 'none',
      backgroundColor: 'transparent',
      marginRight: '10px',
    }
    const enabledStyle = {
      color: 'black',
      fontSize: '20px',
      border: 'none',
      backgroundColor: 'transparent',
      marginRight: '10px',
    }
    if(currentPage === Value){
      return enabledStyle;
    }
    else {
      return disabledStyle;
    }
    //  value  Colorchange   ?
  }

  return (
    <div className="main">
      <Link to="/main">
        <div className="pageName">
          <strong>DASOM</strong>
        </div>
      </Link>
      <h1></h1>
      <button onClick={() => pageChange('1')} style={Colorchange('1')}>
         
      </button>
      <button onClick={() => pageChange('2')} style={Colorchange('2')}>
         
      </button>
      <button onClick={() => pageChange('3')} style={Colorchange('3')}>
         
      </button>
      {output()}
    </div>
  );
}

export default MyPage;